// value container
$maxWidth: 1920;
$maxWidthContainer: 1440;

// value Font-Size
$minFontSize: 16;
$maxFontSize: 22;
$addSize: $maxFontSize - $minFontSize;

@function count-rem($value) {
	@return $value / $maxFontSize + rem;
}
// COLORS

$white: #fff;
$pink: #FF8991;
$lightBlue: #43E8FF;
$blueBg: #4D5374;
$blueCardBg: #383E57;
$darkBlue: #23273A;
$darkBlueBg: #1F2339;
$lightGray: #A1A6BE;
$purpleGradient: linear-gradient(124.04deg, rgba(182, 26, 255, 0.5) 2%, rgba(0, 26, 255, 0.5) 95%), #656CFF;
$blueGradient: linear-gradient(90deg, #43E8FF 30%, #00FFD1);